/* General Form Styling */
.order-form {
  width: 50%; /* Set a max-width for better visibility */
  margin: 20px auto; /* Center the form with some margin */
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px; /* Increased margin for better spacing */
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input[type="number"], 
input[type="date"], 
input[type="time"], 
select {
  width: calc(100% - 20px); /* Ensure full width minus padding/border */
  height: 50px; /* Fixed height for input fields */
  padding: 10px; /* Consistent padding */
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px; /* Slightly increase font size for better readability */
  box-sizing: border-box; /* Include padding and border in element's total width/height */
}

/* Confirm Button */
.confirm-btn {
  display: block;
  width: 100%; /* Full width */
  height: 50px; /* Fixed height for the button */
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px; /* Increased font size for the button */
  margin-top: 10px;
}

.confirm-btn:hover {
  background-color: #333;
}

/* Modal Styling */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  width: 400px; /* Control the size of the modal */
  max-width: 90%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.modal-content h3 {
  margin-top: 0;
}

.modal-content p {
  margin-bottom: 10px;
}

/* Modal Button Container */
.modal-button-container {
  display: flex; /* Use flexbox to align buttons next to each other */
  justify-content: space-between; /* Space them evenly */
  margin-top: 20px; /* Add some spacing from the content above */
}

/* Modal Buttons */
.modal-content button {
  width: calc(50% - 5px); /* Adjust width to take half of the container width, minus some space */
  height: 50px; /* Fixed height for modal buttons */
  background-color: #000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal-content button:hover {
  background-color: #333;
}
